<template>
  <div id="publish">
    <el-row v-loading="loading">
      <el-col :offset="2" :span="20">
        <el-form ref="form" :model="form" :rules="formRules" label-position="top">

          <el-form-item label="" prop="icon">
            <div class="upload-demo">

              <img src="../../static/loading.svg" v-lazyload class="show-image" :data-src="form.icon" data-err="https://fakeimg.pl/350x200/?font=lobste&text=DAPP" v-if="form.icon && form.icon != ''">

              <el-input minlength="1" maxlength="150" v-model="form.icon" placeholder=""></el-input>

              <div class="el-upload__tip">
                 <a href="https://img9.top/" target="_blank"></a>,  480px  480px, jpg/png <a href="https://tinypng.com/" target="_blank">tinypng.com</a> 
              </div>
            </div>
          </el-form-item>

          <el-form-item label="" prop="name">
            <el-input minlength="1" maxlength="150" v-model="form.name" placeholder=""></el-input>
          </el-form-item>

          <el-form-item label="" prop="dtype">
            <el-select v-model="form.dtype" placeholder="">
              <el-option
                v-for="item in dtypeOptions"
                :key="item"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="" prop="url">
            <el-input minlength="1" maxlength="350" v-model="form.url" placeholder=""></el-input>
          </el-form-item>

          <el-form-item label="" prop="userName">
            <el-input minlength="1" maxlength="150" v-model="form.userName" placeholder=""></el-input>
          </el-form-item>

          <el-form-item label="" prop="addr">
            <el-input minlength="1" maxlength="210" v-model="form.addr" placeholder=""></el-input>
          </el-form-item>

          <el-form-item label="txhash" prop="txhash">
            <el-input minlength="1" maxlength="210" v-model="form.txhash" placeholder="txhash"></el-input>
          </el-form-item>

          <el-form-item label="" prop="desc">
            <el-input type="textarea" :autosize="{ minRows: 5, maxRows: 10}"
                      minlength="1" maxlength="600"
                      v-model="form.desc" placeholder="">
            </el-input>
          </el-form-item>

          <el-form-item label="" prop="week">
            <el-select v-model="form.week" placeholder="">
              <el-option
                v-for="item in weekOptions"
                :key="item"
                :label="item"
                :value="item">
              </el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="" prop="reason">
            <el-input type="textarea" :autosize="{ minRows: 5, maxRows: 10}"
                      minlength="1" maxlength="600"
                      v-model="form.reason" placeholder="">
            </el-input>
          </el-form-item>

          <el-form-item label="" prop="reasonimg">
            <div class="upload-demo">
              <img src="../../static/loading.svg"
                   v-lazyload
                   :data-src="form.reasonimg"
                   class="show-image-bj"
                   data-err="https://fakeimg.pl/350x200/?font=lobste&text=oops"
                   v-if="form.reasonimg && form.reasonimg != ''"/>

              <el-input minlength="1" maxlength="150" v-model="form.reasonimg" placeholder=""></el-input>

              <div class="el-upload__tip">
                 <a href="https://img9.top/" target="_blank"></a>, jpg/png <a href="https://tinypng.com/" target="_blank">tinypng.com</a> 
              </div>
            </div>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="submitNode" v-if="!queryId">DApp</el-button>
            <el-button type="primary" @click="modNode" v-if="queryId">DApp</el-button>
            <el-button @click="resetForm('form')" v-if="!queryId"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>

  </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'

export default {
  data () {
    return {
      queryId: null,
      dtypeOptions: ['', '', ''],
      weekOptions: ['', '', '', '', '', '', '', ''],
      // M 30k 0.03M
      iconMaxSize: 0.03,
      // M 100k 0.03M
      reasonimgMaxSize: 0.05,
      loading: false,
      form: {
        icon: '',
        name: '',
        dtype: '',
        url: '',
        userName: '',
        addr: '',
        txhash: '',
        desc: '',
        week: '',
        reason: '',
        reasonimg: ''
      },
      formRules: {
        icon: [
          {required: true, message: '', trigger: 'blur'}
        ],
        name: [
          {required: true, message: '', trigger: 'blur'},
          {min: 1, max: 150, message: ' 1  150 ', trigger: 'blur'}
        ],
        dtype: [
          {required: true, message: '', trigger: 'blur'},
          {min: 1, max: 150, message: ' 1  150 ', trigger: 'blur'}
        ],
        url: [
          {required: true, message: '', trigger: 'blur'},
          {min: 1, max: 350, message: ' 1  350 ', trigger: 'blur'}
        ],
        userName: [
          {required: true, message: '', trigger: 'blur'},
          {min: 1, max: 150, message: ' 1  150 ', trigger: 'blur'}
        ],
        addr: [
          {required: true, message: '', trigger: 'blur'},
          {min: 1, max: 210, message: ' 1  210 ', trigger: 'blur'}
        ],
        txhash: [
          {min: 1, max: 210, message: ' 1  210 ', trigger: 'blur'}
        ],
        desc: [
          {min: 1, max: 600, message: ' 1  600 ', trigger: 'blur'}
        ],
        week: [
          {required: true, message: '', trigger: 'blur'},
          {min: 1, max: 150, message: ' 1  150 ', trigger: 'blur'}
        ],
        reason: [
          {required: true, message: '', trigger: 'blur'},
          {min: 1, max: 600, message: ' 1  600 ', trigger: 'blur'}
        ],
        reasonimg: [
          {required: true, message: '', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    // 
    returnHome: function () {
      this.$router.push({name: 'index'})
    },
    submitNode: function () {
      var vueThis = this
      if (_.isEmpty(vueThis.accounts)) {
        vueThis.showErr('');
        return false;
      }
      vueThis.$refs['form'].validate((valid) => {
        if (valid) {
          var f = vueThis.form;
          let params = [f.name, f.dtype, f.icon, f.url, f.userName, f.addr, f.txhash, f.desc, f.week, f.reason, f.reasonimg];
          vueThis.loading = true
          vueThis.$store.dispatch('callNew', {funcName: 'addDapp', params: params}).then((ret) => {
            vueThis.loading = false
            vueThis.showSuccess('DApp');
            vueThis.$router.push({name: 'mine'})
          }, (error) => {
            vueThis.loading = false
            vueThis.showErr('DApp' + error);
          });
        } else {
          vueThis.showErr('');
        }
      })
    },
    modNode: function () {
      var vueThis = this
      if (_.isEmpty(vueThis.accounts)) {
        vueThis.showErr('');
        return false;
      }
      vueThis.$refs['form'].validate((valid) => {
        if (valid) {
          var f = vueThis.form;
          let params = [vueThis.queryId, f.name, f.dtype, f.icon, f.url, f.userName, f.addr, f.txhash, f.desc, f.week, f.reason];
          vueThis.loading = true
          vueThis.$store.dispatch('callNew', {funcName: 'updateDapp', params: params}).then((ret) => {
            vueThis.loading = false
            vueThis.showSuccess('DApp');
            vueThis.$router.push({name: 'mine'})
          }, (error) => {
            vueThis.loading = false
            vueThis.showErr('DApp' + error);
          });
        } else {
          vueThis.showErr('');
        }
      })
    },
    getDetail: function (id) {
      let vueThis = this;
      if (!id) {
        return;
      }
      vueThis.loading = true;
      vueThis.$store.dispatch('simulateCallNew', {funcName: 'getDapp', params: [id]}).then((ret) => {
        let data = JSON.parse(ret);
        vueThis.form = data;
        vueThis.loading = false;
      }, (error) => {
        vueThis.loading = false;
        vueThis.showErr('' + error);
      });
    },
    resetForm: function (formName) {
      var vueThis = this;
      vueThis.$refs[formName].resetFields();
    }
  },
  created () {
    this.queryId = this.$route.query.id;
    this.getDetail(this.queryId)
  },
  computed: mapState([
    'accounts'
  ])
}
</script>

<style>
  #publish {
  }

  #publish  .el-form--label-top .el-form-item__label{
    padding: 0px;
  }

  #publish .show-image{
    border-radius:20px;
    width: 120px;
    height: 120px;
    margin-bottom: 10px;
    object-fit: cover;
    box-shadow:0 4px 3px -2px rgba(0,0,0,.8), 0 0 3px 1px rgba(0,0,0,.05);
    transition:all .4s;
    vertical-align: middle;
    border-style: none;
  }

  #publish .show-image-bj{
    max-width: 220px;
    max-height: 320px;
    margin-bottom: 10px;
    object-fit: cover;
    vertical-align: middle;
    border-style: none;
  }

  #publish .show-input{
    background-color:#fbfdff;
    border:1px dashed #c0ccda;
    border-radius:4px;
    box-sizing:border-box;
    cursor:pointer;
    padding: 10px;
  }

  #publish .text-here {
    font-family: 'Poppins', sans-serif;
    font-size: 14px;
    line-height: 1.75;
    color: #757575;
    font-weight: 400;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  #publish .post_meta_top{
    color: #666;
    font-size: 14px;
    font-style: italic;
  }
</style>



