<template>
  <div id="detail" v-loading="loading">
    <el-row v-if="detail">
      <!---->
      <el-col :offset="2" :span="20">
        <div style="display: flex">
          <div class="content">
            <img src="../../static/loading.svg" v-lazyload class="icon" :data-src="detail.icon" data-err="https://fakeimg.pl/350x200/?font=lobste&text=DAPP" :alt="detail.name">
          </div>
          <div style="flex: 1" class="content">
            <h2 style="margin-top:10px;margin-bottom:8px;color:#606266">
              {{detail.name}}
              <span v-if="detail.value != '0'" style="color:#E6A23C;font-size: 14px;"> {{detail.valueNas}}nas </span>
            </h2>
            <p class="type">{{detail.dtype}}</p>
            <p>{{detail.userName|kong}} {{detail.week}} </p>
            <p><a :href="detail.url" target="_blank">{{detail.url}}</a></p>
            <p><a :href="'https://explorer.nebulas.io/#/address/' + detail.addr" target="_blank">{{detail.addr}}</a></p>
            <p>{{detail.desc|kong}}</p>
          </div>
        </div>
      </el-col>

      <el-col :offset="2" :span="20">
        <br>
      </el-col>

      <el-col :offset="2" :span="20" style="background-color:#f3f3f3;padding:10px;border-radius:4px;border: 1px solid #888">
        <el-row>
          <el-col :span="24">
            <p style="color:#909399;text-align: center;font-size: 16px;">-- DApp    --</p>
            <p style="color:#909399;text-align: center">
              <img v-lazyload
                   src="../../static/loading.svg"
                   :data-src="detail.reasonimg"
                   data-err="https://fakeimg.pl/350x200/?font=lobste&text=oops"
                   style="max-width: 100%;">
            </p>
            <p style="padding: 10px;color:#606266;font-size: 16px;letter-spacing: 2px;font-weight: 500">{{detail.reason|kong}}</p>
          </el-col>
        </el-row>
        <br>
      </el-col>

      <el-col :offset="2" :span="20">
        <br>
      </el-col>

      <!---->
      <el-col :offset="2" :span="20">
        <h3></h3>
      </el-col>
      <el-col :offset="2" :span="20" v-for="(item, k) in detail.comments" :key="k" style="padding-top:10px;padding-bottom:10px;border-top:1px solid #E4E7ED">
        <div style="float: left;line-height: 35px">
          <vue-letter-avatar :name='item.nickname' size='30' :rounded='true' />&nbsp;
        </div>
        <div style="height: 35px;line-height: 35px">
          <span style="float: left"  class="post_meta_top">
            {{item.nickname}} - {{item.ctDes}}
            <span v-if="item.value != 0" style="color:#E6A23C;">
              &nbsp;: {{item.valueNas}} Nas
            </span>
          </span>
        </div>
        <p class="post_content">{{item.comment}}</p>
      </el-col>

      <!---->
      <el-col :offset="2" :span="20">
        <el-form :model="subform" ref="subform" :rules="subformRules">
          <el-form-item label="" prop="comment">
            <el-input type="textarea" minlength="1" maxlength="150"
                      v-model="subform.comment" auto-complete="off"
                      :autosize="{ minRows: 6, maxRows: 6}"></el-input>
          </el-form-item>

          <el-form-item label="" prop="nikename">
            <el-input minlength="1" maxlength="50" v-model="subform.nikename"></el-input>
          </el-form-item>

          <el-form-item label="">
            <el-input type="number" min="0" v-model="subform.reward" auto-complete="off">
              <template slot="append">Nas</template>
            </el-input>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="subMyComment(detail.id)"></el-button>
          </el-form-item>
        </el-form>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import nebulas from 'nebulas'

export default {
  data () {
    return {
      loading: false,
      subformTile: '',
      queryId: null,
      subform: {
        comment: '',
        nikename: '',
        reward: 0
      },
      subformRules: {
        comment: [
          {required: true, message: '', trigger: 'blur'},
          {min: 1, max: 150, message: ' 1  150 ', trigger: 'blur'}
        ],
        nikename: [
          {required: true, message: '', trigger: 'blur'},
          {min: 1, max: 50, message: ' 1  50 ', trigger: 'blur'}
        ]
      },
      detail: null
    }
  },
  methods: {
    getDetail: function (id) {
      let vueThis = this;
      vueThis.loading = true;
      console.log('getDetail', id)
      vueThis.$store.dispatch('simulateCallNew', {funcName: 'getDapp', params: [id]}).then((ret) => {
        let data = JSON.parse(ret);
        vueThis.detail = data;
        let d = new Date(vueThis.detail.ct)
        vueThis.detail.ctDes = d.toLocaleDateString() + ' ' + d.toLocaleTimeString();
        vueThis.detail.valueNas = nebulas.Unit.fromBasic(vueThis.detail.value, 'nas');
        vueThis.detail.valueNas = _.replace(vueThis.detail.valueNas, '"', '');

        _.forEach(vueThis.detail.comments, function (item) {
          let d = new Date(item.ct)
          item.ctDes = d.toLocaleDateString() + ' ' + d.toLocaleTimeString();
          item.valueNas = nebulas.Unit.fromBasic(item.value, 'nas');
          item.valueNas = _.replace(item.valueNas, '"', '');
        });
        vueThis.loading = false;
      }, (error) => {
        vueThis.loading = false;
        vueThis.showErr('' + error);
      });
    },
    // 
    subMyComment: function (id) {
      let vueThis = this
      if (_.isEmpty(vueThis.accounts)) {
        vueThis.showErr('');
        return false;
      }
      vueThis.$refs['subform'].validate((valid) => {
        if (valid) {
          let f = vueThis.subform;
          let params = [id + '', f.nikename, f.comment + ''];
          vueThis.loading = true;
          vueThis.$store.dispatch('callNew', {money: f.reward, funcName: 'addComment', params: params, goods: {name: ''}}).then((ret) => {
            vueThis.loading = false;
            console.log('', ret);
            vueThis.showSuccess(', 3');
            _.delay(function () {
              vueThis.subform.comment = '';
              vueThis.subform.reward = 0;
              vueThis.getDetail(vueThis.queryId);
            }, 3000)
          }, (error) => {
            console.log('', error);
            vueThis.loading = false;
            vueThis.showErr('' + error);
          });
        } else {
          vueThis.showErr('');
        }
      })
    }
  },
  created () {
    this.queryId = this.$route.query.id;
    this.getDetail(this.queryId)
  },
  computed: mapState([
    'accounts'
  ])
}
</script>

<style>
  #detail {
  }

  #detail .post_content {
    padding: 10px;
    font-family: 'Poppins', sans-serif;
    font-size: 14px;
    line-height: 1.75;
    color: #757575;
    font-weight: 400;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  #detail .post_meta_top{
    color: #666;
    font-size: 14px;
    font-style: italic;
  }

  #detail .icon{
    display: inline-block;
    float: left;
    width: 240px;
    height: 240px;
    border-radius:20px;
    object-fit: cover;
    box-shadow:0 4px 3px -2px rgba(0,0,0,.8), 0 0 3px 1px rgba(0,0,0,.05);
    transition:all .4s;
    vertical-align: middle;
    border-style: none;
    margin-right: 20px;
    margin-bottom: 20px;
  }

  #detail .content{
    font-weight: 200;
    letter-spacing: 2px;
    font-family: "Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","",Arial,sans-serif;;
  }

  #detail .content .type{
    color:  #999;
    font-size: 16px;
  }

  #detail .content p{
    margin-top:7px;
    margin-bottom:7px;
    color: #606266;
    font-size: 14px;
  }

  #detail .content p a{
    color: #409EFF;
  }
</style>



