<template>
  <div id="index">
    <h1 style="text-align: center;color:#909399">
       DApp  
    </h1>


    <div style="border: 1px solid #ffeeba;padding: 10px;margin-bottom: 10px;background-color: #fff3cd;color:#856404">
      ()<span style="font-size: 18px;font-weight: bold"> 0.1nas </span>
    </div>

    <div style="border: 1px solid #ddd;padding: 10px;color: #606266">
      !
      
    </div>


    <el-row v-loading="loading" v-for="(item, k) in showList" :key="k">
      <el-col :span="24">
        <h3 :class="item.class"> {{item.name}} </h3>
      </el-col>

      <el-col :span="4" v-for="(o, index) in item.data" :key="index" style="padding: 5px">
        <el-card :body-style="{ padding: '8px'}" @click.native="showDetail(o.id)" shadow="never">
          <div style="width: 100%;text-align: center">
            <img src="../../static/loading.svg" v-lazyload class="app-image" :data-src="o.icon" data-err="https://fakeimg.pl/350x200/?font=lobste&text=DAPP">
          </div>
          <div style="padding: 5px;">
            <p class="post_title">
                <span class="post_meta_top">
                  {{ o.dtype }}
                </span>
              {{ o.name }}
            </p>
            <p class="post_meta_top" style="color:#E6A23C">
              &nbsp; {{o.valueNas}}nas 
            </p>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <div style="text-align: right;margin-top: 10px;">
      <el-pagination
        background
        layout="prev, pager, next"
        @current-change="changePage"
        :current-page.sync="pageInfo.current"
        :page-size="pageInfo.size"
        :total="pageInfo.total">
      </el-pagination>
    </div>

  </div>
</template>

<script>
import {mapState} from 'vuex'
import _ from 'lodash'
import nebulas from 'nebulas'

export default {
  data () {
    return {
      loading: false,
      pageInfo: {current: 1, size: 6},
      listAll: [],
      showList: {
        'hot': {name: '', class: 'hot-title', data: []},
        'normal': {name: '', class: 'all-title', data: []}
      }
    }
  },
  methods: {
    // 
    showDetail: function (id) {
      this.$router.push({name: 'detail', query: {id: id}})
    },
    // 
    getList (o) {
      var vueThis = this
      vueThis.loading = true;
      vueThis.$store.dispatch('simulateCallNew', {funcName: 'getAllDapp'}).then((ret) => {
        let data = JSON.parse(ret);

        vueThis.listAll = data;
        // 
        _.forEach(vueThis.listAll, function (item) {
          let d = new Date(item.ct)
          item.ctDes = d.toLocaleDateString() + ' ' + d.toLocaleTimeString();

          item.valueNas = nebulas.Unit.fromBasic(item.value, 'nas');
          item.valueNas = _.replace(item.valueNas, '"', '');
        });
        // 
        vueThis.listAll = _.orderBy(vueThis.listAll, ['ct'], ['desc']);
        // 
        let hotSort = _.orderBy(vueThis.listAll, function (item) {
          return item.comments.length;
        }, ['desc']);
        vueThis.showList['hot'].data = _.slice(hotSort, 0, vueThis.pageInfo.size);

        vueThis.loading = false;
        vueThis.changePage(1);
      }, (error) => {
        vueThis.loading = false;
        vueThis.showErr('' + error);
      });
    },
    // []
    changePage (pageNum) {
      let vueThis = this;
      vueThis.loading = true;
      vueThis.showList['normal'].data = [];
      // 
      let p = vueThis.pageInfo;
      p.current = !pageNum ? 1 : pageNum;
      let maxPage = Math.ceil(p.total / p.size);
      p.current = _.min([p.current, maxPage])
      p.total = _.get(vueThis.listAll, 'length', 0);

      let start = (p.current - 1) * p.size;
      let end = _.min([p.current * p.size, p.total]);

      vueThis.showList['normal'].data = _.slice(vueThis.listAll, start, end);
      vueThis.loading = false;
    }
  },
  created () {
    this.getList()
  },
  computed: mapState([
    'user'
  ])
}
</script>

<style>
  #index{
  }

  #index .hot-title{
    padding: 10px;
    background-color: rgba(255, 69, 0, 0.68);
    color: white;
    text-align: center;
  }
  #index .all-title{
    padding: 10px;
    background-color: rgba(0, 186, 189, 1);
    color: white;
    text-align: center;
  }

  #index .app-image{
    border-radius:20px;
    width: 135px;
    height: 135px;
    margin-bottom: 10px;
    object-fit: cover;
    box-shadow:0 4px 3px -2px rgba(0,0,0,.8), 0 0 3px 1px rgba(0,0,0,.05);
    transition:all .4s;
    vertical-align: middle;
    border-style: none;
  }

  #index .app-image:focus, #index .app-image:hover{
    transform: scale(1.05);
  }

  #index .post_title {
    color: #4c4c4c;
    font-weight: 400;
    font-size: 12px;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
  }

  #index .post_meta_top {
    color: #999;
    font-weight: 400;
    font-size: 12px;
  }
</style>



